import React, { Component } from "react";
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import { isEmail } from "validator";
import AuthService from "../services/auth.service";
const required = value => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert">
        ⵄⴰⵎⵎⴰⵔ ⵍ ⵅⴰⵡⵉ:This field is required!
      </div>
    );
  }
};
const email = value => {
  if (!isEmail(value)) {
    return (
      <div className="alert alert-danger" role="alert">
       ⵍⵄⵓⵏⵡⴰⵏ ⵓⵔ ⵉⵚⵃⵉ: This is not a valid email.
      </div>
    );
  }
};
const vnachname = value => {
  if (value.length < 3 || value.length > 10) {
    return (
      <div className="alert alert-danger" role="alert">
         ⵉⵅⵙⵙⴰ ⵉⵙⵎ ⴰⴷⵉⴽⵙ ⴽⵟⵕ ⵍ  3 ⴷ ⴽⴰⵍ ⵏ 10 ⵏ ⵍⴰⵃⵔⴰⴼ:The nachname must be between 3 and 10 characters.
      </div>
    );
  }
};
const vvorname = value => {
  if (value.length < 3 || value.length > 10) {
    return (
      <div className="alert alert-danger" role="alert">
         ⵉⵅⵙⵙⴰ ⵉⵙⵎ ⴰⴷⵉⴽⵙ ⴽⵟⵕ ⵍ  3 ⴷ ⴽⴰⵍ ⵏ 10 ⵏ ⵍⴰⵃⵔⴰⴼ:The vorname must be between 3 and 10 characters.
      </div>
    );
  }
};
const vpassword = value => {
  if (value.length < 3 || value.length > 40) {
    return (
      <div className="alert alert-danger" role="alert">
        ⵉⵅⵙⵙⴰ ⵜⵙⴰⵔⵓⵜ  ⴰⴷⵉⴽⵙ ⴽⵟⵕ ⵍ  3 ⴷ ⴽⴰⵍ ⵏ 40 ⵏ ⵍⴰⵃⵔⴰⴼ The password must be between 3 and 40 characters.
      </div>
    );
  }
};
export default class Register extends Component {
  constructor(props) {
    super(props);
    this.handleRegister = this.handleRegister.bind(this);
    this.onChangeNachname = this.onChangeNachname.bind(this);
    this.onChangeVorname = this.onChangeVorname.bind(this);
    this.onChangeEmail = this.onChangeEmail.bind(this);
    this.onChangePassword = this.onChangePassword.bind(this);
    this.state = {
      nachname: "",
      vorname: "",
      email: "",
      password: "",
      successful: false,
      message: ""
    };
  }
   onChangeNachname(e) {
    this.setState({
      nachname: e.target.value
    });
  }
  onChangeVorname(e) {
    this.setState({
      vorname: e.target.value
    });
  }
  onChangeEmail(e) {
    this.setState({
      email: e.target.value
    });
  }
  onChangePassword(e) {
    this.setState({
      password: e.target.value
    });
  }
  handleRegister(e) {
    e.preventDefault();
    
    this.setState({
      message: "",
      successful: false
    });
    this.form.validateAll();
    if (this.checkBtn.context._errors.length === 0) {
      AuthService.register(
        this.state.nachname,
        this.state.vorname,
        this.state.email,
        this.state.password
      ).then(
        response => {
          this.setState({
            message: response.data.message,
            successful: true
          });
        },
        error => {
          const resMessage =(error.response && error.response.data &&   error.response.data.message) || error.message || error.toString();
          this.setState({
            successful: false,
            message: resMessage
          });
          
        }
      );
    }
  }
  render() {
    return (
      <div className="col-md-12">
        <div className="card card-container">
          <img
            src="//ssl.gstatic.com/accounts/ui/avatar_2x.png"
            alt="profile-img"
            className="profile-img-card"
          />
          <Form
            onSubmit={this.handleRegister}
            ref={c => {
              this.form = c;
            }}
          >
            {!this.state.successful && (
              <div>
                <div className="form-group">
                  <label htmlFor="nachname">ⵍⴽⵊⵢⴰ/First Name/الإسم العائلي</label>
                  <Input
                    type="text"
                    className="form-control"
                    name="nachname"
                    value={this.state.nachname}
                    onChange={this.onChangeNachname}
                    validations={[required, vnachname]}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="vorname">ⵉⵙⵎ/Last Name الإسم الشخصي</label>
                  <Input
                    type="text"
                    className="form-control"
                    name="vorname"
                    value={this.state.vorname}
                    onChange={this.onChangeVorname}
                    validations={[required, vvorname]}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="email">ⴰⴷⵄⴰⵔ ⵏ ⵍ ⴱⵓⵙⵜⴰ/Email/البريد الإلكتروني </label>
                  <Input
                    type="text"
                    className="form-control"
                    name="email"
                    value={this.state.email}
                    onChange={this.onChangeEmail}
                    validations={[required, email]}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor="password">ⵜⴰⵙⴰⵔⵓⵜ/Password /كلمة السر</label>
                  <Input
                    type="password"
                    className="form-control"
                    name="password"
                    value={this.state.password}
                    onChange={this.onChangePassword}
                    validations={[required, vpassword]}
                  />
                </div>
                <div className="form-group">
                  <button className="btn btn-primary btn-block">ⵙⴰⵊⵊⴰⵍ/Sign Up</button>
                </div>
              </div>
            )}
            {this.state.message && (
              <div className="form-group">
                <div
                  className={
                    this.state.successful
                      ? "alert alert-success"
                      : "alert alert-danger"
                  }
                  role="alert"
                >
                  {this.state.message}
                </div>
              </div>
            )}
            <CheckButton
              style={{ display: "none" }}
              ref={c => {
                this.checkBtn = c;
              }}
            />
          </Form>
        </div>
      </div>
    );
  }
}
